.buy-theft-fire-btn {
    background-color: #00A1E4; /* Vibrant blue */
    color: white;
    font-family: 'Heebo', sans-serif;
    font-size: 17px;
    font-weight: bold;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;
  }
  
  .buy-theft-fire-btn:hover {
    background-color: #008CBA; /* Slightly darker blue */
  }